body {
  margin: 40px 0 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

section {
  padding: 60px 0 20px;
}

#profile-photo {
    text-align: center;
}

#profile-photo img {
    width: 25em;
    border-radius: 100%;
    margin: 20px 0;
}

#profile-photo figcaption {
    text-align: justify;
}

#curriculum-button {
    padding: 10px 30px;
    background-color: black;
    color: white;
    border: 1px solid black;
    border-radius: 20px;
}

#curriculum-button:hover {
    background-color: white;
    color: black;
    cursor: pointer;
}

@media (max-width: 992px) {
    #profile-photo img {
        width: 15em;
    }
}
#skills {
    text-align: center;
}

#list-skills {
    padding: 20px 0;
}

#list-skills {
    list-style: none;
}

#list-skills li {
    padding: 10px 5px;
    margin: 3px;
    background-color: darkgrey;
    border-radius: 5px;
    cursor: pointer;
}

#list-skills li:hover {
    background-color: black;
    color: white;
}
img {
    width: 250px;
}

#blog {
    text-align: center;
}

#blog a {
    text-decoration: none;
}

#blog figcaption {
    padding: 20px 0 10px;
    text-align: justify;
    color: black;
}

#blog article {
    color: black;
    /*border-radius: 10px;*/
}

@-webkit-keyframes expand{
	from {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
	to {
		-webkit-transform: scale(1.05);
		        transform: scale(1.05);
	}
}

@keyframes expand{
	from {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
	to {
		-webkit-transform: scale(1.05);
		        transform: scale(1.05);
	}
}

#blog article:hover{
    color: white;
    -webkit-animation-name: expand;
            animation-name: expand;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

#portfolio {
    text-align: center;
}

#portfolio a {
    margin: 5px 10px;
}
#infos h2,
#infos-github {
    text-align: center;
}

#infos-github {
    padding: 10px 0;
    font-size: 0.8em;
}

#infos p {
    text-align: justify;
}

#quote {
    font-size: 0.8em;
    font-style: italic;
}

#quote-author {
    font-size: 0.9em;
    text-align: end;
}

.medium-icon {
    width: 4rem;
}

figure {
    margin-top: 20px;
}
#contact {
    text-align: center;
}

.tiny-icon {
    width: 2em;
}

#contact-social a {
    display: inline-block;
    margin: 0 5px;
}

#contact-social h4 {
    margin: 40px 0 0;
}
